.home__container{
    row-gap: 5rem;
}

.home__content {
  grid-template-columns: 116px 2fr 1fr;
  padding-top: 9.5rem;
  column-gap: 2rem;
  align-items: center;
}

.home__social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}

.home__social-icon {
  font-size: 1.25rem;
  color: var(--title-color);
}

.home__social-icon:hover {
  color: var(--title-color-dark);
}

.home__title {
  font-size: 3rem;
  margin-bottom: var(--mb-0-25);
}

.home__hand {
  width: 38px;
  height: 38px;
  margin-left: 0.4rem;
}

.home__subtitle {
  position: relative;
  font-size: var(--h3-font-size);
  padding-left: 5rem;
  font-weight: var(--font-normal);
  margin-bottom: var(--mb-1);
}

.home__subtitle::before {
  content: "";
  position: absolute;
  width: 70px;
  height: 1px;
  background: var(--text-color);
  left: 0;
  top: 1rem;
}

.home__description {
  max-width: 400px;
  margin-bottom: var(--mb-3);
  padding-bottom: 1rem;
  text-align: justify;
  /* border: 1px solid; */
}

.home__image {
  background: url("../../assets/Bashir.JPG");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 9px rgba(255 255 255 / 30%);
  order: 1;
  justify: center;
  width: 300px;
  height: 300px;
  animation: profile__animate 8s ease-in-out infinite 1s;
}

@keyframes profile__animate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }

  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }

  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}


.home__scroll {
    margin-left: 9.25rem;
}

.scroll{
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0)
    }

    30% {
        transform: translateY(3.75rem)
    }
}

.home__scroll-name{
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-left: 2px;
   margin-right: var(--mb-0-25)
}

.home-scroll-down{
  font-size: 1.25rem;
  color: var(--title-color)
}

@media screen and (max-width: 992px) {
  .home__content{
    grid-template-columns: 100px repeat(2, 1fr);
    column-gap: 1.25rem;
    padding-top: 6rem;
  }

  .home__hand{
    width: 26px;
    height: 26px;
  }

  .home__subtitle{
    padding-left: 2.8rem;
    margin-bottom: var(--mb-1)
  }

  .home__subtitle::before{
    width: 42px;
    top: 0.9rem;
  }

  .home__description{
    max-width: initial;
    margin-bottom: var(--mb-2-5)
  }

  .home__image{
    width: 250px;
    height: 250px;
    box-shadow: inset 0 0 0 8px rgba(255 255 255 / 30%);
  }
}

@media screen and (max-width: 768px) {
  .home__content{
    grid-template-columns:  0.5fr 3fr;
    padding-top: 3.5rem;
     /* border: 1px solid; */
  }

  .home__image{
    order: initial;
    justify-self: initial
  }

  .home__data{
    grid-column: 1/3
  }

  .home__image{
    width: 200px;
    height: 200px;
    box-shadow: inset 0 0 0 6px rgba(255 255 255 / 30%);
  }

  .home__scroll{
    display: none;
  }

  .button__flex{
    margin-top: -2rem;
    margin-bottom: 4rem;
  }
}

@media screen and (max-width: 576px) {
  .button__flex{
    padding: 1rem 1.5rem;
  }
}