.services__container {
  display: grid;
  grid-template-columns: repeat(2, 270px);
  justify-content: center;
  column-gap: 1.8rem;
}

.services__content {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: var(--container-color);
  padding: 6rem 0 2rem 2.5rem;
  /* box-shadow: 4rem 45px 5rem rgba(0, 0, 0, 0.1); */
  /* border-radius: 1.25rem; */
}

.services__icon {
  display: block;
  font-size: 2rem;
  color: var(--title-color);
  margin-bottom: var(--mb-1);
}

.services__title {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-1);
  font-weight: var(--font-medium);
}

.services__button {
  color: var(--title-color);
  font-size: var(--small-font-size);
  display: inline-flex;
  align-items: center;
  column-gap: 0.25rem;
}

.services_button_icon {
  font-size: 1rem;
  transition: 0.3s;
}
.services__button:hover .services_button_icon {
  cursor: pointer;
  transform: translateX(00.25rem);
}

.services__button:hover {
  cursor: pointer;
}

.services__modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: var(--z-modal);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  opacity: 0;
  visibility: hidden;
  transition: 0.4s;
}
.show__modal {
  opacity: 1;
  visibility: visible;
}

.services__modal-content {
  width: 500px;
  position: relative;
  background-color: var(--container-color);
  padding: 3rem 2rem 2rem;
  border-radius: 1.5rem;
}

.services__modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 1.5rem;
  color: var(--title-color);
  cursor: pointer;
}

.services__modal-title,
.services__modal-description {
  text-align: center;
}
.services__modal-title {
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1);

}

.services__modal-description {
  font-size: var(--small-font-size);
  padding: 0 0.5rem;
  margin-bottom: 1rem;
}

.services__modal-services {
  row-gap: 0.75rem;
}

.services__modal-service {
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
}

.services__modal-icon {
  color: var(--title-color);
  font-size: 1.1rem;
}

.services__modal-info {
  font-size: var(--small-font-size);
}

/* Active Modal */
.active__modal {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 992px) {
  .services__container {
    grid-template-columns: repeat(2, 218px);
  }
  .services__modal {
    top: 40rem;
  }
}

@media screen and (max-width: 768px) {
  .services__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .services__content{
    padding: 3.5rem 0.5rem 1.25rem 1.5rem;
  }
  .services__icon{
    font-size: 1.4rem;
  }
}

@media screen and (max-width: 576px) {
  .services__modal-content{
    padding: 4.5rem 1.5rem 2.5rem;
  }
  .services__modal-description{
    padding: 0;
  }
}

@media screen and (max-width: 350px) {
    .services__container{
        grid-template-columns: max-content;
    }
    .services__content{
        padding-right: 7rem;
    }
}
